import { UserModel } from "../../model/user";
const userModel = new UserModel();
    export default {
        name: "car_check_add",
        data() {
            return {

            }
        },
        methods: {
            goAddCar() {
                uni.redirectTo({
                    url: "/pagesA/my_add_car/my_add_car"
                });
            },
             _getUserCar() {
                userModel.getCars().then(res => {
                    let data = res.data;
                    if (data.length > 0 ) {
                        uni.navigateTo({
                            url : `/pagesB/car_check/car_check`
                        })
                    }
                });
            },
        },
        onShow()
        {
           this._getUserCar();
        }
    }
